@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
.page {
  font-family: Roboto, sans-serif;
}

.page__body {
  margin: 0;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  position: relative;
}

.nav__item:first-of-type {
  margin-left: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
  transition: color .3s ease-in-out;
  display: flex;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.container {
  grid-template-columns: repeat(var(--cards-column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    --cards-column-count: 2;
  }
}

@media (width >= 768px) {
  .container {
    --cards-column-count: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --cards-column-count: 4;
  }
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  box-sizing: border-box;
  padding-inline: 16px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding-top: 32px;
  padding-bottom: 16px;
  transition: transform .3s ease-in-out;
}

.card:hover {
  transform: scale(1.2);
}

.card__picture {
  height: 134px;
  margin-bottom: 40px;
  padding-inline: 3px;
  display: flex;
}

.card__image {
  width: 100%;
}

.card__title {
  color: #060b35;
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s ease-in-out;
}

.card:hover .card__title {
  color: #34568b;
}

.card__subtitle {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price :first-child {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__cost {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  box-sizing: border-box;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  padding-block: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: color, background-color .3s ease-in-out;
  display: inline-block;
}

.card__button:hover {
  cursor: pointer;
  color: #00acdc;
  background-color: #fff;
}

